import {IconStyle} from '@here/hds-icon';
import React from 'react';
import {VerticalNavigationItem} from '@here/hds-vertical-navigation';
import {HDSAccordionItemProps} from '../Accordion/HDSAccordionItem.js';

// eslint-disable-next-line no-new
new VerticalNavigationItem();

interface HDSVerticalNavigationItemProperties extends HDSAccordionItemProps {
  selected?: boolean;
  avatarUrl?: string;
  avatarName?: string;
  iconStyle?: IconStyle;
}

const HDSVerticalNavigationItem = React.forwardRef(
  (props: HDSVerticalNavigationItemProperties, ref) => {
    const {selected, avatarUrl, avatarName, iconStyle, className, iconCategory, children, ...rest} =
      props;

    return (
      <hds-vertical-navigation-item
        ref={ref}
        {...(className && {class: className})}
        {...(selected && {selected})}
        {...(avatarUrl && {'avatar-url': avatarUrl})}
        {...(avatarName && {'avatar-name': avatarName})}
        {...(iconCategory && {'icon-category': iconCategory})}
        {...(iconStyle && {'icon-style': iconStyle})}
        {...rest}
      >
        {children}
      </hds-vertical-navigation-item>
    );
  }
);

HDSVerticalNavigationItem.defaultProps = {
  avatarUrl: '',
  avatarName: '',
  selected: false,
};

export default HDSVerticalNavigationItem;
